import { render } from '@testing-library/preact';
import { expect } from 'chai';
import { View } from '..';

describe('Test View component', () => {
  it('render', async function () {
    const { container } = render(
      <View border="2px solid red">
        <div style="width: 50px; height: 50px;" />
      </View>
    ) as any;
    // Test that the inner content is rendered.
    expect(container.firstChildElement).not.null;
  });

  it('adds aria-label using accessibleLabel prop', () => {
    const label = 'test label';
    const { getByLabelText } = render(<View accessibleLabel={label} />);
    expect(getByLabelText(label)).not.null;
  });

  it('adds aria-labelledby using ariaLabelledBy prop', () => {
    const labelId = 'testId';
    const label = 'test label';
    const { getByLabelText } = render(
      <View ariaLabelledBy={labelId}>
        <label id={labelId}>{label}</label>
      </View>
    );
    expect(getByLabelText(label)).not.null;
  });
});
